.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}

.subtotal__gift {
    display: flex;
    align-items: center;
}

.subtotal__gift > input {
    margin-right: 5px;
}

.subtotal > button {
    cursor: pointer;
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734;
    color: #111;
}

@media only screen and (min-width: 220px) and (max-width: 768px) {
    .subtotal {
        width: 82vw;
        margin-right: 3rem;
        border-radius: 1px;
    }
    
    .subtotal__gift {
        display: flex;
        align-items: center;
    }
    
    .subtotal__gift > input {
        margin-right: 5px;
    }
    
    .subtotal > button {
        height: 20px;
        margin-top: 16px;
    }
}
